.widget {
  position: fixed;
  right: 0;
  bottom: 20px;

  &__link {
    display: flex;
    align-items: center;
    padding: 31px 24px;

    font-weight: 600;
    font-size: 16px;
    line-height: 22px;

    border-radius: 8px 0 0 8px;
    background-color: $color-red-orange;
    color: $color-default-white;

    transition: background-color $trans-default, color $trans-default;

    @include vp-767 {
      padding: 16px 18px;
    }

    span {
      margin-left: 5px;

      @include vp-767 {
        display: none;
      }
    }

    svg {
      flex-shrink: 0;

      width: 24px;
      height: 24px;
      margin-left: 20px;

      @include vp-767 {
        margin-left: 8px;
      }
    }

    @include hover-focus {
      background-color: $color-flamingo;
      color: rgba($color-default-white, 0.9)
    }

    @include active {
      background-color: $color-free-speech-red;
      color: rgba($color-default-white, 0.9)
    }

    &.disabled {
      background-color: $color-silver;
      color: rgba($color-default-white, 0.9)
    }
  }
}
